
<template >
    <section>
        <div class="col-md-12 p-0 mt-2">
        <div class="theme-primary partition-full">
            <span class="p-name text-white">Timesheet</span>
            <span class="p-actions float-right">
                <a id="AddManually" href="javascript:;" class="p-action-btn text-white addman btn-info"><span>Expert</span></a>
                <a id="AddManually" href="javascript:;" class="p-action-btn text-white addman btn-info"><span>This week</span></a>
                <a id="AddManually" href="javascript:;" class="p-action-btn text-white addman btn-info"><i class="fa fa-delete mx-2"></i></a>
            </span>
        </div>
    </div>
        <div class="listing py-3 px-3">
        <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
            <template v-slot:tgslot-TimeSheetTable="{ data }">
                <div class="listing">
                <div class="col-12 mb-2">
                    <div class="mr-2 d-inline-block">
                        <span class="legends-purple colorboxsquare"></span>Automatic
                    </div>
                    <div class="mr-2 d-inline-block">
                        <span class="legends_low colorboxsquare"></span>Manual
                    </div>
                    <div class="mr-2 d-inline-block">
                        <span class="legends_medium colorboxsquare"></span>TimeSheet
                    </div>
                    <div class="mr-2 d-inline-block">
                        <span class="legends_profile colorboxsquare"></span>Leave
                    </div>
                    <div class="mr-2 d-inline-block">
                        <span class="legends_high colorboxsquare"></span>Holiday
                    </div>
                    <div class="mr-2 d-inline-block">
                        <span class="lg-pausedresumed colorboxsquare"></span>PausedResumed
                    </div>
                </div>
            </div>
                <tg-list  
                    :RenderRowActionMethod="renderActions" 
                    IdentityColumn ="track_id"
                    ModuleName="TimeTrack" 
                    :HeaderText="$t('Brand')"
                    SubModuleCode="Timesheet"          
                    :SortExp="SortExp"  
                    :SortBy="SortBy"   
                    :IsShowAction="false"  
                    :ListData="TimesheetList" 
                    :HeaderData="Headers" 
                    :showCheckBox="true"   
                    :NorecordfoundText="$t('NoRecordfound')" 
                    :HeaderButtons="listheaderbuttons"
                    >
                    <template v-slot:slotdata="{data}" v-for="item in TimesheetList" >
                        <template v-if="data.column.COLUMN_NAME == 'break_tt'">
                                        <!-- <input type="text" value={{item.break_tt}}> -->
                                        <input v-model="item.break_tt"> 
                        </template>
                    </template>
                </tg-list>                             
            </template>
        </dynamic-form>

    
        
        <a id="btnCancel" title="Cancel" class="btn btn-danger d-none"><i class="fa fa-close pr-2"></i><span>Cancel</span></a>
        <a id="btnCopyLastWeek" isapprover="0" class="btn btn-warning formbtn mr-2 text-dark" onclick="" name="" title='Copy to other Users'><i class="fa fa-copy pr-2"></i>Copy to other Users</a>
                        <a id="btnCopyLastWeek" isapprover="0" class="btn btn-warning formbtn mr-2 text-dark" onclick="" name="" title='Copy Last week'><i class="fa fa-copy pr-2"></i>Copy Last week</a>
                        <a id="btnSaveAsDraft" isapprover="0" class="btn btn-warning formbtn text-dark mr-2" onclick="" name="" title='SaveAsDraft'><i class="fa fa-save pr-2"></i>SaveAsDraft</a>
                        <a id="btnSendForApproval" isapprover="0" class="btn btn-success text-white formbtn mr-2" onclick="" title='SendForApproval'><i class="fa fa-send pr-2"></i>SendForApproval</a>
    </div>
    <!-- <loader :is-visible="isLoading"></loader> -->


    </section>
    </template>
<script>
import DataService from '../../services/DataService';   
export default
    {
        data() {
            return {
                FormSchema: [
                    {
                        layoutType: "single",
                        Data: [
                            // {
                            //     astype: "DateField",
                            //     label: "Date Range",
                            //     name: "date_range",
                            //     value: (new Date()),
                            //     placeholder: "This week",
                            //     //validationRules: "required",
                            //     config: {
                            //         mode: "dateTime", 
                            //         is24hr: true,
                            //         format: "YYYY-MM-DD - hh:mm",
                            //         minDate: (new Date())
                            //         //maxDate: ....
                            //     }
                            // },
                            // {
                            //     astype: "SelectField",
                            //     label: "User",
                            //     name: "user_id",
                            //     value: "",
                            //     placeholder: "",
                            //     config: {
                            //         options: [],
                            //     },
                            //     validationRules: {
                            //         //"required": true
                            //     },
                            // },
                            {
                                astype: "SlotField",
                                label: "",
                                name: "TimeSheetTable",
                                value: "",
                                placeholder: "",
                                config: {
                                    options: [],
                                },
                                validationRules: {
                                    //"required": true
                                },
                            },
                            
                        ]
                    }
                ],
                buttons: [
                    {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa pr-2'></i>ShowTimeSheet",
                    isVisible: true,
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa pr-2'></i>btnCopyLastWeek",
                    isVisible: true,
                }
                ],             
                DataLoaded:0,
                PageNumber: 1,
                PageSize: 10,
                SortBy: '',
                SortExp: '',
                noRecord: false,
                TotalRecords: 0,
                isLoading:false,
                TimesheetList: [],
                UserId:null,
                Headers: [{
                    "COLUMN_NAME": "break_hh_mm",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Projects",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "break_tt",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Mon, Dec",
                    "DISPLAY_ORDER": 2,

                },          
            ],                
            }
        },
        created:async function () {
            this.DataLoaded = 1;
            await this.GetUsersForTimesheet();
            var VueObj=this;
            VueObj.FormSchema[0].Data[1].value = VueObj.GetUserInfo.ID;
            VueObj.FetchData();
        },
        mounted: function() {
            var VueObj=this;
                console.log('mounted')
            },
        methods: {
            async GetUsersForTimesheet() {
                var vm = this;
                vm.isLoading = true;
                var url = `companyId=270936&loggedUserId=769083&userType=CA&userId=769083&moduleName=`;
                await DataService.GetUsersForTimesheet(url)
                    .then(function (response) {

                        response.data.result.forEach(item => {
                            vm.obj = {
                                name: item.username,
                                value: item.useR_ID
                            }
                            vm.FormSchema[0].Data[1].config.options.push(vm.obj);
                        });

                        vm.isLoading = false;
                    });
            },
            async ListTimesheetForWeek(formfields) {
                var VueObj = this;
                var fDate = VueObj.$options.filters.formatDateTime(formfields.date_range)
                var tDate = VueObj.$options.filters.formatDateTime(formfields.date_range)
               VueObj.FetchData();
                // var url = `userId=270936&dateFrom=${formfields.date_range}&dateTo=&selectedUserId=${formfields.user_id}&IsAudit=0&showCheckbox=&newTrackId=0`;
                var url = `userId=769083&dateFrom=${fDate}&dateTo=${tDate}&selectedUserId=769083&IsAudit=0&showCheckbox=&newTrackId=0`;
                //270936,'02/02/2023','02/20/2023',270936,769083,0,0,0,null,null,0
                await DataService.ListTimesheetForWeek(url).then(response => {
                    //vm.TimesheetList = response.data.listing[0]

                    if (response.data != null && response.data != "") {                   
                        VueObj.TimesheetList = response.data.listing;
                        // $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        // $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        VueObj.noRecord = false;
                    }
                    else 
                    {
                        VueObj.noRecord = true;
                        VueObj.TimesheetList = [];
                    }
                });
            },
            async onSubmit(formfields, e) {
                var vm = this;
                //this.ListTimesheetForWeek(formfields);
                vm.FetchData();

            },
            renderActions: function(row) {
            var self = this;
            },
             FetchData: async function(){ 
                var VueObj = this;
                VueObj.isLoading = true;  
                var fDate = '12/05/2023 12:55 PM';
                    var tDate = '12/05/2023 12:55 PM';
                //const params = this.getRequestParams(this.pageNumber, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.brandname);      
                var url = `userId=${VueObj.GetUserInfo.ID}&dateFrom=${fDate}&dateTo=${tDate}&selectedUserId=${VueObj.FormSchema[0].Data[1].value}&IsAudit=0&showCheckbox=&newTrackId=0`;
                await DataService.ListTimesheetForWeek(url).then(response => {

                    VueObj.isLoading=false;
                VueObj.Headers.forEach((column) => {
                    column.settings = null;
                    if(column.COLUMN_NAME == 'break_tt'){
                        column.settings = {
                                    isInSlot: true
                                }
                    }
                });

                    
                    if (response.data != null && response.data != "" && response.data.listing.length>0) {   
                        //vueObj.Headers = response.data.result.schema;
                        VueObj.Headers.forEach((column) => {
                            if (column.COLUMN_NAME == 'break_tt') {
                                column.DISPLAY_NAME = response.data.listing[0].break_tt
                                
                            }
                        });


                        VueObj.TotalRecords=response.data.listing.length;                
                        VueObj.TimesheetList = response.data.listing;
                        VueObj.TotalPages = 1;
                        VueObj.CurrentPage = 1;
                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        VueObj.noRecord = false;

                        // $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        // $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        VueObj.noRecord = false;
                    }
                    else 
                    {
                        VueObj.noRecord = true;
                        VueObj.TimesheetList = [];
                    }
                    
                });
            }
        }
    }
</script>
